.navLink {
  display: flex;
  border-bottom: 5px solid transparent;
  white-space: nowrap;
  height: 100%;
  cursor: pointer;
  a {
    padding: 20px 0.5rem;
    font-weight: 500;
    font-size: 16px;
    color: white;
    text-decoration: none;
  }

  &:hover {
    border-bottom: 5px solid $headerBorderColor;

    a {
      color: $headerBorderColor;
    }
  }

  &.active {
    border-bottom: 5px solid $headerBorderColor;
    a {
      color: $headerBorderColor;
    }
  }
}

@media (max-width: 1769px) {
  .navLink {
    padding: 0;
    height: auto;
    font-size: 0.5rem;
    width: 100%;
    max-width: 15rem;
    text-align: left;
    cursor: pointer;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      padding: 10px 0.5rem;
      font-weight: 500;
      font-size: 16px;
      color: white;
      text-decoration: none;
    }

    &:hover {
      border-bottom: 5px solid $headerBorderColor;

      a {
        color: $headerBorderColor;
      }
    }

    &.active {
      border-bottom: 5px solid $headerBorderColor;
      a {
        color: $headerBorderColor;
      }
    }
  }
}
@media (max-height: 500px) {
  .navLink {
    a {
      padding: 0.5rem;
    }
  }
}
