.side-menu {
  width: 240px;
  border-right: 1px solid $dividers;
  min-height: calc(100vh - 64px);
  transition: 0.5s ease;
  //   min-height: 90vh;
  padding: 18px 30px;
  &_cross {
    // background: white;
    height: 30px;
    width: 30px;
    display: none;
  }

  ul {
    list-style: none;

    li a {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin: 0.5rem;
      white-space: nowrap;
      padding-left: 0.6rem;
      color: black;
      text-decoration: none;
      display: flex;
      justify-content: space-between;
    }

    .ActiveProductLink {
      a {
        color: $headerColor;
      }
      .list-number {
        color: white;
        background: $headerColor;
      }
    }

    .list-number {
      margin-left: 0.3rem;
      background: #cdd4e0;
      border-radius: 10px;
      font-size: 12px;
      padding: 2px 5px;
      &.less {
        background: $danger;
        color: white;
      }
    }
  }

  &_head {
    & > a {
      gap: 0.5rem;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      color: black;
      text-decoration: none;
      cursor: pointer;
      .activeImg {
        display: none;
      }
      .normal {
        display: block;
      }
    }
    &.activeSideMenuLink {
      .activeImg {
        display: block;
      }
      .normal {
        display: none;
      }
      h3 {
        color: $headerColor;
      }
    }
  }

  &_single {
    margin: 21px 0;
    &:nth-child(1) {
      margin-top: 0;
    }
  }

  h3 {
    font-weight: bold;
    font-size: 14px;
  }

  &.sourcingSideMenu {
    .inActive {
      .normalImage {
        display: block;
      }
      .activeImage {
        display: none;
      }
    }
    .ActiveSourceLink {
      .normalImage {
        display: none;
      }
      .activeImage {
        display: block;
      }
      h3 {
        color: $headerColor;
      }
    }
  }
  &.productSideMenu {
    .inActive {
      .normalProdImg {
        display: block;
      }
      .activeProdImg {
        display: none;
      }
    }
    .ActiveProductLink {
      .normalProdImg {
        display: none;
      }
      .activeProdImg {
        display: block;
      }
      h3 {
        color: $headerColor;
      }
    }
  }
}

@media (max-width: 1000px) {
  .side-menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 40%;
    height: 100%;
    z-index: 10;
    box-shadow: 0 0 1.3rem rgba(0, 0, 0, 0.301);
    background: white;

    &_cross {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      position: absolute;
      top: 18px;
      right: 30px;
      .line {
        background: black;
        height: 5px;
        width: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        // transform: translate(-50%, -50%);
        // transform-origin: bottom;
      }
      .l1 {
        transform: translate(-50%, -50%) rotate(45deg);
      }
      .l2 {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }

    &.hideSide {
      transform: translateX(-100vw);
    }
    &.showSide {
      transform: translateX(0vw);
    }
  }
}

@media (max-width: 600px) {
  .side-menu {
    width: 100%;
  }
}
