.showSideMenu {
  display: none;
}
@media (max-width: 1000px) {
  .showSideMenu {
    position: absolute;
    top: 2rem;
    left: 30px;
    height: 25px;
    width: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .line {
      height: 5px;
      width: 100%;
      background: black;
    }
  }
}
