.shippingMethodModal {
  width: 100% !important;
  max-width: 896px !important;
  label {
    flex: 1 1 150px;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #6c757e;
  }
  .pass_single {
    margin-bottom: 8px;
  }
  select {
    padding: 9px 7px;
    flex: 1 1 523px;
    border: 1px solid #f4f5f8;
    border-radius: 3px;
    max-width: 523px;
  }
  .okbutton {
    padding: 12px 53px;
    background: $headerColor;
    color: white;
    border: none;
    border-radius: 3px;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
  }
  .pass_foot {
    border: none;
  }
  .tableDiv {
    border: 1px solid $dividers;
    // border-top: none;
    width: 100%;
    overflow: auto;
    max-height: 264px;
    table {
      min-width: 550px;
      width: 100%;
    }
    th,
    td {
      font-weight: normal;
      font-size: 14px;
      line-height: 16px;
      height: 44px;
      border: 1px solid $dividers;
      white-space: nowrap;
      border-bottom: none;
      border-left: none;
      &:nth-last-child(1) {
        border-right: none;
      }
    }
    th {
      color: #6c757e;
      background: #f4f5f8;
      border-top: none;
    }
    td {
      padding: 0 13px;
      text-align: center;
      input[type="radio"] {
        margin-right: 13px;
      }
      &:first-child {
        display: flex;
        // justify-content: center;
        align-items: center;
        text-align-last: left;
      }
    }
  }
}
