.tabSection {
  .tabHead {
    border: 1px solid $dividers;
    button {
      border-top: none;
      border-bottom: none;
      border-left: none;
      background: $listHover;
      border-bottom: 5px solid $listHover;
      padding: 0 13px;
      min-width: 103px;
      &.active {
        background: white;
        border-bottom: 5px solid $headerColor;
      }
    }
  }
  .tabBody {
    padding: 24px 29px;
    border: 1px solid $dividers;
    border-top: none;
    min-height: 3rem;
    // &.details {
    // }
    .details_firstImage {
      margin: 27px 0 11px;
    }
    img {
      width: 100%;
      max-width: 960px;
    }
  }

  .facebook {
    .topInfo {
      margin-bottom: 21px;
      display: flex;
      gap: 19px;
      justify-content: flex-start;
      align-items: center;
      p {
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        color: #7987a1;
        span {
          color: black;
        }
      }
    }
    .postLink {
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 20px;
      }
    }
  }
}
