.alert {
  display: flex;
  align-items: center;
  padding: 7px 14px;
  display: inline-block;
  font-weight: 500;
  font-size: 14px;
  border-radius: 3px;
  margin-bottom: 15px;
  &-icon {
    margin-right: 7px;
  }
  &.warn {
    background: #fff2c9;
    color: #ff7300;
  }
  &.notLoggedInAlert {
    margin-bottom: 0;
    margin-top: 15px;
    border: 1px solid #ff7300;
    border-radius: 3px;
  }
  button {
    border: none;
    background: none;
    color: $headerColor;
    font-weight: 500;
    font-size: 13px;
    line-height: 15px;
  }
}
