.billing-page {
  .page-head {
    align-items: center;
    flex-wrap: wrap;
  }
  &_head {
    display: flex;
    flex-wrap: wrap;
    gap: 11px;
    & > button {
      height: 40px;
      padding: 0 17px;
      border: none;
      background: $headerColor;
      font-weight: bold;
      font-size: 14px;
      color: white;
      border-radius: 3px;
    }
  }
  .selection-bar {
    display: flex;
    flex-wrap: wrap;
    border: 1px solid $dividers;
    border-radius: 3px;
    select {
      background: white;
      width: auto;
      border: none;
      border-radius: 0;
    }
    input {
      background: white;
      border: none;
      height: 100%;
    }
    .input {
      padding-left: 15px;
      display: flex;
      align-items: center;
      gap: 5px;
      img {
        height: 1rem;
      }
    }
    #payStatus,
    .input {
      border-right: 1px solid $dividers;
    }
  }
  button.expOrder {
    padding: 0 17px;
    border: none;
    color: white;
    background: $headerColor;
    font-weight: bold;
    font-size: 14px;
  }
  .page-content__body {
    padding-left: 7px;
    padding-right: 7px;
    width: 100%;
    display: flex;
    height: 90%;
    flex-direction: column;
  }
  .pageCounter {
    margin-top: auto;
  }
}

.billing-table {
  max-height: 20rem;
  overflow: auto;
  width: calc(100vw - 322px);
  max-width: 1616px;
  border: 1px solid $dividers;
  border-radius: 3px;
  margin: 13px 0;
  table {
    width: 100%;
    button {
      border: none;
      background: none;
      color: $headerColor;
    }
  }
  thead {
    text-align: left;
    background: $listHover;
    color: #7987a1;
  }
  td,
  th {
    padding: 17px 24px;
    border-bottom: 1px solid $dividers;
  }
  tr:nth-last-child(1) td {
    border: none;
  }
  td {
    white-space: nowrap;
    font-weight: normal;
    font-size: 13px;
  }
  .orderNumber {
    color: #0060df;
  }
  .payStatus {
    padding: 7px;
    border-radius: 20px;
    &.success {
      color: white;
      background: #3bb001;
    }
  }
}

@media (max-width: 1000px) {
  .billing-table {
    width: calc(100vw - 322px + 240px);
  }
}
