.source-details {
  position: relative;
  &__path {
    display: flex;

    gap: 24px;
    flex-wrap: wrap;
    a,
    span {
      color: $headerColor;
    }
    a,
    p {
      font-weight: 500;
      font-size: 14px;
    }
    p {
      // color: #596882 !important;
      color: #596882;
    }
  }
  &__product {
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
    justify-content: space-between;

    .product-ratings {
      display: flex;
      //   align-items: center;
      gap: 7px;
      .ratingCount {
        margin-left: 10px;
        color: #0060df;
      }
    }
    .like-rate {
      margin-bottom: 12px;
      display: flex;
      gap: 13px;
    }
    .heart {
      color: $danger;
    }
    .product-likes {
      display: flex;
      align-items: center;
      //   margin-left: auto;
    }
  }
  .textDets {
    max-width: 500px;
    height: 100%;
    .dangerSpan {
      font-weight: 500;
      font-size: 16px;
      line-height: 30px;
      color: $danger;
    }
    &_head {
      h2 {
        font-weight: 500;
        font-size: 16px;
      }
    }
    &_pricing {
      background: #f4f5f8;
      border-radius: 3px;
      display: flex;
      flex-wrap: wrap;
      gap: 1rem;
      justify-content: space-between;
      align-items: center;
      padding: 14px 14px 10px;
      span {
        font-weight: 500;
        font-size: 25px;
        line-height: 30px;
      }
      .qty {
        color: #7987a1;
        font-weight: 500;
        font-size: 14px;
        line-height: 30px;
      }
    }
    &_orderSpecs {
      margin: 15px 0;
      .shipping_from {
        select {
          width: 100%;
          max-width: 395px;
          padding: 6px;
          background: white;
          font-weight: 500;
          font-size: 12px;
        }
      }
    }
  }
  &__main {
    margin: 20px 0;
    display: flex;
    // flex-wrap: wrap;
    gap: 1rem;
    justify-content: space-between;
  }

  .floatingDiv {
    border: 1px solid $dividers;
    border-radius: 8px;
    width: 363px;
    // max-width: 363px;
    .endingPara {
      span {
        font-weight: 700;
      }
      span:nth-child(2) {
        color: #3bb001;
      }
      span:nth-child(3) {
        color: $headerColor;
      }
    }
    .place-order-link {
      background: $headerColor;
      // display: flex;
      // justify-content: center;
      // align-items: center;
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;
      display: block;
      text-align: center;
      color: white;
      padding: 12px;
      border-radius: 3px;
      margin-top: 11px;
    }

    &__table {
      height: 290px;
      overflow: auto;
    }
    hr {
      border: none;
      border-top: 1px solid #7987a1;
      margin-bottom: 10px;
    }
    hr.bottom-hr {
      margin: 10px 0;
    }

    padding: 15px 24px;
    p {
      font-weight: normal;
      font-size: 14px;
      line-height: 20px;
      &:nth-child(1) {
        display: flex;
        justify-content: space-between;
      }
      b:nth-child(1) {
        font-weight: normal;
        span {
          text-decoration: underline;
        }
      }
      span {
        font-weight: 500;
      }
    }
    h3 {
      font-weight: bold;
      font-size: 16px;
      line-height: 20px;
    }
    tr {
      margin-bottom: 8px !important;
    }
  }
  &__cart {
    table {
      width: 100%;
    }
    tbody {
      padding: 7px;
    }
    th {
      font-weight: normal;
      font-size: 13px;
      line-height: 15px;
      color: #7987a1;
      padding: 5px;
    }
    td {
      text-align: center;
      font-weight: normal;
      font-size: 13px;
      line-height: 20px;
    }
    .qty-col {
      width: 5rem;
      margin: 1rem 0;
    }
    .color-col {
      width: 3rem;
      .color-button {
        margin: 0 auto;
      }
    }
    .size-col {
      width: 3rem;
    }
    th,
    td {
      white-space: nowrap;
      input {
        width: 75px;
        height: 34px;
        border: 1px solid $dividers;
        margin: 0 auto;
      }
    }
  }
  .sizes {
    &_btns {
      display: flex;
      flex-wrap: wrap;
      gap: 7px;
    }
  }
  .colors {
    &_btns {
      gap: 7px;
      display: flex;
      flex-wrap: wrap;
    }
  }
  .shipping_method {
    .selectLikeDiv {
      position: relative;
      border: 1px solid $dividers;
      border-radius: 3px;
      height: 34px;
      padding: 11px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      span {
        font-weight: 500;
        font-size: 12px;
      }
      // &__drop {
      //   position: absolute;
      //   background: white;
      //   top: calc(100% + 6px);
      //   left: -1px;
      //   width: calc(48vw - 2rem);
      //   z-index: 2;
      //   padding: 33px 26px 19px;
      //   border-radius: 3px;
      //   border: 1px solid $dividers;
      //   max-width: 896px;
      //   // transition: display 0.2s ease 1s;
      //   transition-delay: 2s;
      //   // display: none;
      // }
      // &:hover {
      //   .selectLikeDiv__drop {
      //     display: block;
      //   }
      // }
    }
    // .selectDropButton {
    // }
  }

  .spec {
    margin: 6px 0;
    display: flex;
    flex-wrap: wrap;
    .title {
      width: 21%;
      font-weight: 500;
      font-size: 12px;
      line-height: 30px;
      color: #7987a1;
    }
    .title + * {
      flex: 1 1 auto;
    }
    &__buttons {
      padding: 0 4px;
      display: flex;
      flex-wrap: wrap;
      gap: 11px;
      button {
        display: flex;
        gap: 7px;
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        color: white;
        justify-content: center;
        align-items: center;
        white-space: nowrap;
        height: 40px;
        width: 220px;
        background: #ff7300;
        img {
          transform: scale(0.9);
        }
        &:nth-child(1) {
          background: $headerColor;
        }
      }
    }
  }

  .spec-quantity {
    .quantity {
      display: flex;
    }
    .box {
      height: 34px;
      width: 35px;
      // border-radius: 3px;
      border: 1px solid $dividers;
    }
    .show {
      border-left: none;
      border-right: none;
    }
    button {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .dec {
      font-size: 2rem;
    }
    .inc {
      font-size: 1.2rem;
    }
  }
  .shipping_note {
    align-items: flex-start;
    span {
      font-weight: 500;
      font-size: 12px;
      line-height: 18px;
    }
    a {
      color: #0060df;
    }
  }
  @import "./sizeBtn";
  @import "./colorBtn";
  @import "./sourceDetTabs";
  @import "./sourceStats";
}

@media (max-width: 1742px) {
  .source-details {
    .textDets {
      max-width: none;
      width: 100%;
      height: auto;

      select {
        width: auto !important;
        max-width: none !important;
      }
    }
  }
}
@media (max-width: 1203px) {
  .source-details {
    &__main {
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
    }
  }
}
