.pageCounter {
  margin-top: 40px;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  .navigator {
    display: flex;
    gap: 5px;
    .left {
      opacity: 0.4;
    }
  }
  .inputer {
    display: flex;
    align-items: center;
  }
  .box {
    background: #f4f5f8;
    width: 36px;
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    border-radius: 3px;
  }
  .first {
    background: $headerColor;
    color: white;
  }
  .whiteBox {
    text-align: center;
    background: white;
    border: 1px solid $dividers;
    /* Chrome, Safari, Edge, Opera */
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }

    /* Firefox */
    &[type="number"] {
      -moz-appearance: textfield;
    }
  }
}
