.modal-overLay {
  padding: 0 1rem;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  transition: opacity 0.3s ease;
  transform: scale(0);
  opacity: 0;
  display: grid;
  place-items: center;
  z-index: 100000000;

  .dark-bg {
    position: absolute;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    top: 0;
    left: 0;
  }

  &.show {
    opacity: 1;
    transform: scale(1);
  }
  .modalCenterDiv {
    border-radius: 3px;
    overflow: hidden;
    box-shadow: 0rem 0rem 2rem rgba(0, 0, 0, 0.5);
    z-index: 10;
    background: white;
    border: 1px solid $dividers;
    flex-direction: column;
  }
  .changePass {
    max-width: 600px;
    width: 100%;
  }
  .pass {
    font-weight: 500;
    display: flex;
    justify-content: flex-start;
    align-items: baseline;
    gap: 1rem;

    // &_head,
    // &_foot,
    // &_body {
    // }

    &_head,
    &_body,
    &_foot {
      padding: 0 28px;
      width: 100%;
      height: 61px;
    }
    form {
      width: 100%;
    }
    &_head {
      padding-right: 0;
      width: 100%;
      background: $listHover;
      border-top-left-radius: 3px;
      border-top-right-radius: 3px;
      border-bottom: 1px solid $dividers;
      display: flex;
      justify-content: space-between;
      align-items: center;
      span {
        font-weight: bold;
        font-size: 16px;
      }
    }
    &_cross {
      height: 100%;
      width: 77px;
      position: relative;
      .line {
        pointer-events: none;
        position: absolute;
        top: 50%;
        left: 50%;
        background: $dividers;
        height: 3px;
        width: 21px;
      }
      .l1 {
        transform: translate(-50%, -50%) rotate(45deg);
      }
      .l2 {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
      &:hover {
        background: $dividers;
        .line {
          background: #000;
        }
      }
    }
    &_body {
      height: auto;
    }
    &_foot {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      gap: 7px;
      margin-top: 26px;
      border-top: 1px solid $dividers;
      input {
        border: none;
        background: $listHover;
        font-weight: 500;
        font-size: 14px;
        height: 40px;
        width: 86px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 3px;
      }
      input[type="submit"] {
        color: white;
        background: $headerColor;
      }
    }
    &_single {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      label {
        flex: 0 1 134px;
      }
      input {
        padding: 0 14px;
        margin: 5px 0;
        max-width: 408px;
        flex: 1 1 400px;
        height: 40px;
        border: 1px solid $dividers;
        border-radius: 3px;
      }
    }
  }

  input:focus {
    background: #eee1ff;
    border: 2px solid $headerColor;
  }

  @import "./shippOverlay";
}
