.twoWay {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  // .rdrDateDisplayWrapper {
  //   width: 20rem;
  // }

  // .rdrMonthAndYearWrapper,
  // .rdrMonths.rdrMonthsVertical {
  //   display: none;
  // }

  .singleWay {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  .wayTwo {
    max-width: 630px;
    .twoWay_single {
      padding: 22px 27px;
    }
  }
  .wayOne {
    flex: 1 1 5rem;
    // order: 1;
  }
  &_single {
    border: 1px solid $dividers;
    border-radius: 3px;
    // min-height: 20rem;
  }
  &_notices {
    display: flex;
    flex-wrap: wrap;
    min-height: auto;

    .mask {
      overflow: hidden;
      width: 247px;
      height: 100%;
      position: relative;
    }

    .notice-content {
      flex: 1 1 18rem;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 23px 34px;
    }
    h4 {
      font-weight: bold;
      font-size: 14px;
    }
    p {
      margin-top: 4px;
      font-weight: normal;
      font-size: 14px;
    }
    .buttons {
      button {
        border-radius: 3px;
        background: $headerColor;
        color: white;
        border: none;
        font-weight: bold;
        font-size: 14px;
        width: 140px;
        height: 40px;
      }
      button:nth-child(2) {
        color: black;
        background: none;
      }
    }
    .notice-Mask {
      height: 100%;
    }
    .notice-QRcode {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
  &_graphs {
    padding: 20px 26px;
    height: 469px;
    .main-chart {
      height: 86%;
    }
    .inputFilter {
      background: #f4f5f8;
      border: 1px solid #cdd4e0;
      border-radius: 3px 0px 0px 3px;
    }
  }
  &_tables {
    overflow: auto;
    height: 367px;
    h3 {
      font-weight: bold;
      font-size: 14px;
      line-height: 16px;
    }
    p {
      font-weight: normal;
      font-size: 14px;
      line-height: 16px;
    }

    button {
      background: none;
      border: none;
      color: $headerColor;
    }

    padding: 20px 26px;
    table {
      width: 100%;
      margin-top: 27px - 12px;
    }
    th {
      text-align: left;
      color: #7987a1;
      font-weight: 500;
    }
    tbody {
      tr:nth-child(odd) {
        background: $listHover;
      }
    }
    th,
    td {
      border: none;
      padding: 12px 17px;
      font-size: 14px;
      line-height: 16px;
    }
    td.country {
      font-weight: 500;
      font-size: 14px;
    }
    // td {

    // }
    td {
      border: 1px solid #cdd4e0;
      border-left: none;
      border-right: none;
      border-top: none;
    }
  }
  &_business {
    border: none;
    .text {
      padding: 15px 28px;
    }
    h2 {
      font-weight: bold;
      font-size: 30px;
      line-height: 35px;

      color: #23282f;
    }
    .para {
      margin-top: 8px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      p.para2 {
        font-weight: bold;
        font-size: 14px;
      }
      span {
        color: #3bb001;
      }
    }
    .bussOps {
      margin-top: 20px;
      gap: 20px;
      display: flex;
      flex-direction: column;
      &_single {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 13px 29px;
        min-height: 109px;
        border: 1px solid $dividers;
        border-radius: 3px;
        flex-wrap: wrap;
        gap: 28px;
      }
      &_text {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex: 1 1 20rem;
      }
      &_buttons {
        display: flex;
        justify-content: space-between;
        align-items: center;
        // width: 100%;
        gap: 40px;
        max-width: 350px;
      }
      .btn-Single {
        display: flex;
        gap: 8px;
        flex-direction: column;
        button {
          width: 155px;
          height: 40px;
          border-radius: 3px;
          background: none;
          border: 1px solid $dividers;
        }
        span {
          font-weight: 500;
          font-size: 14px;
        }
      }
      &_para {
        margin-top: 8px;
      }
      &_li {
        transform: scale(1);
        position: relative;
        padding: 0 1rem;
        &::before {
          content: "";
          height: 6px;
          width: 6px;
          background: $dividers;
          border-radius: 50%;
          position: absolute;
          top: 50%;
          left: 0.3rem;
          transform: translate(0, -50%);
        }
      }
      &_hs {
        span {
          font-weight: 700;
        }
      }
      h4 {
        font-weight: bold;
        font-size: 14px;
      }
      p {
        font-weight: normal;
        font-size: 13px;
        // line-height: 20px;
      }
      &_image {
        width: 40px;
        height: 40px;
        object-fit: cover;
      }
    }
  }
  &_contact {
    h3 {
      font-weight: 700;
      font-size: 14px;
    }
    p {
      font-family: Roboto;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 16px;
    }
    p.simp {
      margin-top: 4px;
      margin-bottom: 7px;
    }
    p.timings {
      font-weight: 900;
      white-space: nowrap;
    }
    .socialLinks {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      align-items: center;
      &_single {
        flex: 1 1 auto;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        img {
          margin: 10px;
        }
      }
    }
  }
  &_advertise {
    height: 177px;
    position: relative;
    display: flex;
    img {
      object-fit: cover;
    }
    .adv {
      &_bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
      &_sideImg {
        order: 1;
        position: relative;
      }
    }
    .text {
      display: flex;
      flex-direction: column;
      position: relative;
    }
    h3 {
      font-weight: bold;
      font-size: 14px;
    }
    p {
      font-weight: normal;
      font-size: 14px;
      line-height: 16px;
    }
    button {
      margin-top: auto;
      width: 140px;
      height: 40px;
      border: none;
      font-weight: bold;
      font-size: 14px;
      line-height: 16px;
      border-radius: 3px;
      background: #ff7300;
    }
  }
  &_announcement {
    h3 {
      font-weight: bold;
      font-size: 14px;
    }

    &_head {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      button {
        border: none;
        background: none;
        color: #cdd4e0;
        cursor: pointer;
      }
    }

    .announcements {
      max-height: 502px;
      overflow: auto;
      display: flex;
      flex-direction: column;
      gap: 20px;
      margin-top: 19px;
      p {
        line-height: 16px;
        height: 32px;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      &_head {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        gap: 11px;
      }
      &_date {
        font-weight: normal;
        font-size: 14px;
        color: #7987a1;
      }
      &_message {
        font-weight: 500;
        font-size: 14px;
      }
      &_status {
        font-style: italic;
        font-weight: 900;
        font-size: 14px;
        line-height: 16px;
      }
      .hot {
        color: $danger;
      }
      .new {
        color: #3bb001;
      }
    }
  }
  &_FAQ {
    h3 {
      margin-bottom: 8px;
    }
    .FAQ {
      display: flex;
      flex-direction: column;
      gap: 14px;
      p {
        color: black;
      }

      &_single:hover {
        // p {
        color: $headerColor;
        // }
        button {
          color: $headerColor;
          cursor: pointer;
        }
      }

      span {
        margin-left: 8px;
        margin-right: 8px;
        font-style: italic;
        font-weight: 900;
        font-size: 14px;
        line-height: 16px;
      }

      button {
        border: none;
        background: none;
      }

      &_hot {
        color: $danger;
      }
      &_new {
        color: #3bb001;
      }
    }
  }
  &_working {
    h3 {
      display: flex;
      font-weight: bold;
      font-size: 14px;
    }
    .more {
      margin-left: auto;
      font-weight: normal;
      font-size: 14px;
      line-height: 16px;

      color: #cdd4e0;
    }
    ol {
      padding: 1rem;
    }
    .working {
      &_imgDiv {
        position: relative;
        margin-top: 13px;
      }
      .bg {
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
      .btn {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        &:hover {
          transform: translate(-50%, -50%) scale(1.3);
        }
      }
    }
  }
  &_suggestions {
    .flexBox {
      margin-top: 16px;
      display: flex;
      align-items: center;
      gap: 16px;
      input {
        flex: 1 1 auto;
        height: 38px;
        font-weight: normal;
        font-size: 14px;
        padding: 7px 12px;
        border: 1px solid $dividers;
        border-radius: 3;
      }
      &:nth-last-child(1) {
        margin-bottom: 16px;
      }
    }
    .textarea {
      margin: 9px 0;
      textarea {
        padding: 16px;
        width: 100%;
        font-weight: normal;
        font-size: 14px;
        min-height: 97px;
        border: 1px solid $dividers;
        border-radius: 3px;
      }
      span {
        font-weight: normal;
        font-size: 14px;
        line-height: 27px;
        color: #97a3b9;
      }
    }
    button[type="submit"] {
      width: 140px;
      height: 40px;
      background: $headerColor;
      color: white;
      border: none;
      border-radius: 3px;
      font-weight: bold;
      font-size: 14px;
    }
  }
}
@media (max-width: 1903px) {
  .twoWay {
    .buttons {
      margin-top: 2rem;
    }
  }
}
@media (max-width: 800px) {
  .twoWay {
    &_notices {
      .mask {
        width: 100%;
        height: 170px;
      }
      .notice-Mask {
        width: 100%;
        object-fit: cover;
      }
    }
  }
}
@media (max-width: 1539px) {
  .twoWay {
    .wayTwo {
      max-width: none;
    }
    // .wayOne {
    //   // order: 1;
    // }

    &_advertise {
      .text {
        display: flex;
        flex-direction: column;
        position: relative;
        flex: 1 1 auto;
      }
    }
  }
}
@media (max-width: 660px) {
  .twoWay {
    .wayTwo {
      .twoWay_single {
        padding-left: 15px;
        padding-right: 15px;
      }
    }

    &_contact {
      p.timings {
        white-space: normal;
      }
    }
    &_advertise {
      .text {
        display: flex;
        flex-direction: column;
        position: relative;
        flex: 1 1 auto;
      }
    }
  }
}
