.place-orders {
  max-width: 1342px + 16px;
  width: 100%;
  padding: 0 1rem;
  margin: 0 auto;
  margin-top: 50px;

  //   height: 20rem;
  h1 {
    font-weight: 500;
    font-size: 30px;
    line-height: 20px;
  }
  &_mainPara {
    font-weight: normal;
    font-size: 13px;
    margin: 17px auto 42px;
    line-height: 20px;
    max-width: 1035px;
  }
  h1,
  &_mainPara {
    text-align: center;
  }
  .checker {
    position: static;
    .ImgChecker__checkIcon {
      height: 10px;
    }
  }
  main {
    display: flex;
    flex-wrap: wrap;
    gap: 60px;
  }
  .ordersWay {
    margin-bottom: 78px;
    flex: 1 1 auto;
    h2 {
      margin-bottom: 20px;
      font-weight: normal;
      font-size: 20px;
      line-height: 20px;
    }
  }
  .input {
    width: 100%;
    label {
      font-weight: 500;
      font-size: 13px;
      line-height: 30px;
      span {
        font-weight: 400;
      }
    }
    input,
    select {
      width: 100%;
      height: 34px;
      border-radius: 3px;
      border: 1px solid $dividers;
      font-weight: normal;
      font-size: 12px;
      padding: 0 10px;
    }
    select {
      background: none;
    }
    .nameOC {
      font-size: 12px;
      line-height: 30px;
      color: #7987a1;
    }
  }
  .inputPair {
    display: flex;
    // flex-wrap: wrap;
    gap: 24px;
    .input {
      input,
      select {
        width: 100% !important;
        // flex: 1 1 50px;
      }
    }
    &.threePair {
      .input {
        input,
        select {
          width: 33.3333%;
        }
      }
    }
  }
  .inputChecker {
    padding: 18px 7px;
    display: flex;
    flex-direction: column;
    gap: 8px;
    border: 1px solid $dividers;
    border-left: none;
    border-right: none;
    margin: 21px 0;
    &__single {
      display: flex;
      align-items: center;
      gap: 8px;
    }
  }
  .paymentInfo {
    display: flex;
    margin-bottom: 1rem;
    gap: 8px;
    flex-direction: column;
    &__single {
      padding-left: 7px;
      display: flex;
      align-items: center;
      gap: 8px;
    }
  }
  .lastInputPair {
    margin-bottom: 23px;
    padding-bottom: 28px;
    border-bottom: 1px solid $dividers;
  }
  input.submitBtn {
    height: 60px;
    width: 100%;
    border: none;
    background: $headerColor;
    border-radius: 3px;
    color: white;
  }
}

.cartWay {
  max-width: 599px;
  h2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 27px;
    span {
      font-size: 14px;
      line-height: 20px;
      padding: 0 9px;
      border-radius: 30px;
      background: #cdd4e0;
    }
  }
  //   &_summary {
  &_single {
    border-radius: 3px;
    border: 1px solid $dividers;
    padding: 21px 24px;
  }
  &_Divs {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  h3 {
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 13px;
  }
  span {
    font-weight: normal;
    font-size: 14px;
    line-height: 30px;
  }
  b {
    font-weight: bold;
    font-size: 15px;
    line-height: 30px;
    text-align: right;
  }
  .actSummary {
    &_single {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-right: 39px - 27px;
    }
    .g-total {
      color: $headerColor;
    }
    .revenue {
      color: green;
    }
    .shipping {
      color: blue;
    }
  }
  hr {
    border: none;
    border-top: 1px solid $dividers;
    margin: 17px 0;
  }
  &_remark {
    input {
      width: 100%;
      height: 39px;
      padding: 0 12px;
      border: 1px solid $dividers;
      border-radius: 3px;
      margin-top: 6px;
    }
  }
}

.ordDet {
  max-height: 33rem;
  &_title {
    margin-bottom: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    b,
    span {
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
    }
    b:nth-child(2) {
      font-weight: 400;
    }
    span {
      text-decoration: underline;
    }
  }
  &_subTitle {
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    span {
      font-weight: 900;
    }
  }
  h3 {
    margin-top: 12px;
  }
  &_table {
    overflow: auto;
    width: 100%;
    // min-width: 400px;
    height: 18rem;
  }
  table {
    // min-width: 400px;
    width: 100%;
    // width: 400px;
    height: 100%;
    // height: 18rem;
  }
  th {
    text-align: left;
    font-weight: normal;
    font-size: 13px;
    line-height: 15px;
    color: #7987a1;
    padding-bottom: 10px;
    border-bottom: 1px solid #7987a1;
  }
  // tbody tr {
  //   // margin: 12px 0;
  // }
  tr td:not(:first-child) {
    font-weight: 500;
    font-size: 13px;
    line-height: 20px;
    white-space: nowrap;
  }
  &_product {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &_Title {
      font-weight: normal;
      font-size: 13px;
      line-height: 15px;
      height: 30px;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    &_details {
      margin: 12px 0;
      display: flex;
      gap: 12px;
      max-width: 307px;
      img {
        height: 60px;
      }
    }
  }
}

@media (max-width: 1224px) {
  .cartWay {
    max-width: none;
    &_Divs {
      flex-wrap: wrap;
      flex-direction: row;
    }
    &_single {
      flex: 1 1 20rem;
    }
  }
} ;
