* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  &:focus {
    outline: none;
  }
}

$headerColor: #5600e3;
$headerBorderColor: #2dffb3;
$dividers: #cdd4e0;
$danger: #dc2728;
$listHover: #f4f5f8;
$BtnHover: #e5e5e5;

.app {
  position: relative;
  overflow-x: hidden;
  width: 100vw;
  height: 100vh;
}

.container {
  margin: 0 auto;
  max-width: 1920px;
  // max-width: 1366px;
  width: 100%;
  position: relative;
}

.copyright {
  color: #7987a1;
  font-weight: normal;
  border-top: 1px solid $dividers;
  font-size: 14px;
}

.dbutton {
  margin-left: 4px;
  position: relative;
  button {
    margin: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    white-space: nowrap;
  }

  &_icon {
    margin-left: 0.3rem;
  }

  &_drop {
    font-size: 13px;
    position: absolute;
    width: auto;
    top: 110%;
    background: #ffffff;
    border: 1px solid #cdd4e0;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
    border-radius: 3px;
    transition: 0.3s ease;
    transform-origin: top left;
    z-index: 10;
    transform: scale(0);
  }

  &:hover {
    .dbutton_drop {
      transform: scale(1);
    }
  }

  .boxcheck {
    display: flex;
    padding: 6px 10px;
    align-items: baseline;
    input {
      position: relative;
      top: 0.2rem;
      margin-right: 0.5rem;
    }
    label {
      font-weight: 400;
      white-space: nowrap;
    }

    &:hover {
      background: $headerColor;
      label {
        color: white;
      }
    }
  }
}

.td_bold {
  font-weight: bolder;
}

.checker {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 17px;
  height: 16px;
  border: 1px solid $headerColor;
  border-radius: 3px;
  position: absolute;
  top: 7px;
  left: 7px;
  background: white;
}

.ImgChecker__checkIcon {
  width: 10px;
  color: white;
  display: none;
}

button {
  cursor: pointer;
}

.checker.checked {
  background: $headerColor;
  .ImgChecker__checkIcon {
    display: block !important;
  }
}

.ratings {
  display: flex;
  gap: 4.5px;
  .stars_icon {
    width: 17px;
    height: 17px;
    color: #ff7300;
  }
  .stars {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4.5px;
  }
  .unfilled {
    color: #97a3b9;
  }
  p.reviews {
    font-weight: normal;
    font-size: 13px;
  }
}

#root {
  overflow-y: hidden;
}

