header {
  background: $headerColor;
  .container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    font-family: Roboto;
    color: #ffffff;
    padding-left: 33px;
    padding-right: 33px;
    margin: 0 auto;
    .logo {
      padding: 15px 33px 15px 0;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 9px;
    }

    img.logoImg {
      width: 29px;
    }

    h1.logoTxt {
      font-weight: 500;
      font-size: 20px;
      line-height: 23px;
    }

    .nav {
      list-style: none;
      display: flex;
      // flex-wrap: wrap;
      height: 100%;
    }
  }
  .header-container {
    gap: 1.5rem;
  }
}

.burger {
  width: 30px;
  height: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  display: none;
  z-index: 101;
  .line {
    width: 30px;
    height: 5px;
    background: white;
    transition: 0.3s ease;
  }
  &.active {
    .line1 {
      transform: translateY(10px) rotate(45deg);
    }
    .line2 {
      opacity: 0;
    }
    .line3 {
      transform: translateY(-10px) rotate(-45deg);
    }
  }
}

@media (max-width: 1769px) {
  .burger {
    display: flex;
    // margin-left: auto;
  }

  header .container .nav {
    display: flex;
    // flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    max-height: 100vh;
    background: #000;
    z-index: 100;
    transition: 0.3s ease;
    transform: translateY(-300vh);
    overflow: auto;
    &.active {
      transform: translateY(0vh);
    }
  }
}

@media (max-width: 1769px) {
  header {
    .container {
      .logo {
        margin-right: auto;
      }
    }
  }
}
@media (max-width: 956px) {
  header {
    padding-bottom: 1rem;
    .container {
      .logo {
        padding: 15px 0;
      }
    }
  }
  .burger {
    order: 1;
  }
}

@media (max-height: 500px) {
  header .container .nav {
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
  }
}
