.price-page-content {
  margin: 0 auto;
  padding-bottom: 61px;
  h1 {
    font-weight: bold;
    font-size: 21px;
    line-height: 25px;
    margin: 29px;
  }
  .pricing-card {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    // display: grid;
    // grid-template-columns: 399px 399px 399px;
    // grid-template-columns: repeat(auto-fill, minmax(350px, 400px));
    gap: 20px;
    &_single {
      width: 399px;
      max-width: 100%;
      left: 443px;
      flex: 1 1 auto;
      top: 146px;
      background: #ffffff;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
      border-radius: 5px;
      padding: 38px 56px;

      &:hover {
        background: #f4f5f8;
      }
    }
    h3 {
      font-style: normal;
      font-weight: bold;
      font-size: 21px;
      line-height: 25px;
    }
    h2,
    h3 {
      margin: 10px 0;
    }
    h2 {
      font-style: normal;
      font-weight: bold;
      font-size: 25px;
      line-height: 29px;

      color: #23282f;
    }
    button {
      margin-top: 17px;
      height: 45px;
      width: 100%;
      border: none;
      border-radius: 3px;
      font-weight: 500;
      font-size: 14px;
    }
    button.current {
      background: $headerColor;
      color: white;
    }
  }
  .cHead {
    span {
      margin-left: 0.5rem;
      font-size: 16px;
      font-weight: 500;
    }
  }
  .cBody {
    margin-top: 24px;
  }
  .priceADV {
    margin-top: 15px;
    padding: 0 0.5rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 22px;
  }
}
