.userInfo {
  width: 100%;
  max-width: 636px;
  height: 41px;
  margin: 0 0 0 auto;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 25px;
  .search {
    height: 40px;
    width: 100%;
    max-width: 350px;
    position: relative;
    border: 2px solid rgba(255, 255, 255, 0.4);
    border-radius: 3px;
    input {
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      padding-left: 11px;
      padding-right: 11px;
      border: none;
      background: transparent;
      color: white;
      &::placeholder {
        font-size: 12px;
        color: rgba(255, 255, 255, 0.4);
      }
    }
    &.showSearch {
      border: 2px solid transparent;
      input {
        opacity: 0;
        pointer-events: none;
      }
    }
    &_icon {
      font-size: 20px;
      position: absolute;
      top: 50%;
      right: 0.5rem;
      transform: translateY(-50%);
    }
  }
  .language {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    font-weight: 500;
    font-size: 14px;
  }
  .notif {
    position: relative;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    img[alt="active notification"] {
      position: absolute;
      top: 0;
      right: 0;
    }
  }
  .person {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 9px;
    span {
      font-weight: 700;
      font-size: 14px;
    }
  }
}

@media (max-width: 1769px) {
  .userInfo {
    margin: 0;
    // max-width: none;
    flex: 1 1 auto;
    .search {
      // order: 1;
      max-width: none;
    }
    .language {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 0.5rem;
      font-weight: 500;
      font-size: 14px;
    }
    .notif {
      position: relative;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;

      img[alt="active notification"] {
        position: absolute;
        top: 0;
        right: 0;
      }
    }
    .person {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 9px;
      span {
        font-weight: 700;
        font-size: 14px;
      }
    }
  }
}
@media (max-width: 1004px) {
  .userInfo {
    order: 2;
    .search {
      order: 1;
    }
  }
}

@media (max-width: 956px) {
  .userInfo {
    width: 100%;
    flex-wrap: wrap;
    max-width: none;
    height: auto;
    justify-content: center;
    .search {
      max-width: none;
      min-width: 100%;
      margin-left: auto;
    }
  }
}
