.auth-page {
  h3 {
    font-weight: bold;
    font-size: 14px;
  }
  &__brands {
    margin: 10px 0 25px;
    display: flex;
    flex-wrap: wrap;
    gap: 6px;
  }
  .singleBrand {
    overflow: hidden;
    position: relative;
    cursor: pointer;
    width: 160px;
    height: 60px;
    display: grid;
    place-items: center;
    border-radius: 3px;
    background: $listHover;
    border: 1px solid $dividers;
    .over {
      height: 100%;
      width: 100%;
      background: hsla(263, 100%, 45%, 0.8);
      border: 2px solid $headerColor;
      position: absolute;
      top: 0;
      left: 0;
      display: grid;
      place-items: center;
      color: white;
      transition: transform 0.1s ease;
      transform: translate(0%, 100%);
    }
    &:hover {
      .over {
        border-radius: 3px;
        transform: translate(0%, 0%);
      }
    }
  }
  &__table {
    margin: 10px 0 25px;
    overflow: auto;
    border: 1px solid $dividers;
    max-width: 1616px;
    width: calc(100vw - 322px);
    height: 457px;
    table {
      width: 100%;
    }
    thead {
      background: $listHover;
      padding: 16px 22px;
      th {
        color: #7987a1;
        border-bottom: 1px solid $dividers;
        text-align: left;
      }
    }

    th,
    td {
      padding: 16px 22px;
      white-space: nowrap;
    }
  }
}

@media (max-width: 1000px) {
  .auth-page__table {
    max-width: 1616px;
    width: calc(100vw - 322px + 240px);
  }
}
