.source {
  a {
    color: black;
    text-decoration: none;
  }
  &_filters {
    padding: 27px 0 23px;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    gap: 9px;
  }
  &_ads {
    margin-bottom: 26px;
    width: 100%;
    grid-gap: 12px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(230px, 258px));
    img {
      width: 100%;
    }
  }
  button {
    background: none;
  }
  button,
  input {
    height: 40px;
  }
  .button-pair {
    display: flex;
    flex-wrap: wrap;
  }
  input {
    border: none;
    padding-left: 7px;
  }
  button,
  .button-pair1 {
    border-radius: 3px;
    padding: 0 12px;
    border: 1px solid $dividers;
  }
  .button-pair {
    button {
      border: none;
    }
  }
  .search-btn {
    background: $headerColor;
    color: white;
    font-weight: 500;
    font-size: 14px;
  }
  .price-Btn {
    button {
      font-weight: normal;
    }
  }
  .button-pair2 {
    border: 1px solid $dividers;
    border-radius: 3px;
    button {
      border-right: 1px solid $dividers;
    }
    button:nth-last-child(1) {
      border: none;
    }
  }
  .button-pair1 {
    padding-right: 0;
  }
  .input {
    display: flex;
    align-items: center;
    height: auto;
    border-right: 1px solid $dividers;
  }
  &_icon {
    color: $dividers;
  }
  .dbutton {
    margin: 0;
    // &.all-cat {

    // }
    &_icon {
      color: $dividers;
    }
  }
  .sort-button {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 0 12px;
    border: 1px solid $dividers;
    border-radius: 3px;
    position: relative;
    gap: 7px;
    .dbutton {
      position: static !important;
    }
    button {
      border: none;
      padding: 0;
    }
  }
  .clear-btn {
    border-color: $danger;
    color: $danger;
    background: #fef0f0;
  }
  &__ProductList {
    width: 100%;
    display: grid;
    justify-content: space-between;
    grid-template-columns: repeat(auto-fill, minmax(230px, 270px));
    gap: 2rem;
  }
  &__product {
    .image {
      position: relative;
      height: 289px;
      img.poster {
        height: 100%;
      }
      .newTag {
        position: absolute;
        top: 17px;
        left: 17px;
        font-weight: bold;
        font-size: 13px;
        color: white;
        background: #3bb001;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 48px;
        width: 48px;
      }
    }
    .txt {
      display: flex;
      min-height: 130px;
      flex-direction: column;
      justify-content: space-between;
      border: 1px solid $dividers;
      border-top: none;
      padding: 10px 15px 12px;
      button {
        width: 100%;
        border: 1px solid $dividers;
        background: none;
        height: 36px;
      }
      p {
        font-weight: normal;
        font-size: 13px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      .price {
        font-weight: bold;
        font-size: 16px;
      }
      .stats {
        display: flex;
        justify-content: space-between;
        & > div {
          padding: 0.3rem 0;
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 0.3rem;
          margin-bottom: 13px;
        }
      }
    }
    .star {
      color: #ff7300;
    }
    .heart {
      color: #dc2728;
    }
    &:hover {
      box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.3);
      button {
        background: $headerColor;
        color: white;
        border: 1px solid $headerColor;
      }
    }
  }
  @import "./sourceDetImg";
}
