.sourceing-details-images {
  display: flex;
  flex: 1 1 30rem;
  justify-content: flex-start;
  gap: 18px;

  .source-slider {
    display: flex;
    flex-direction: column;
    gap: 4px;
    // height: 97%;
    button {
      height: 19px;
      width: 88px;
      background: #f4f5f8;
      border: none;
      color: #97a3b9;
    }
    .sliderDiv {
      height: auto;
      //   flex: 1 1 auto;
      img {
        width: 88px;
        height: 88px;
      }
    }
    .sliderImages {
      height: 363px;
      overflow: auto;
      display: flex;
      gap: 4px;
      flex-direction: column;
    }
  }
  .source-magnifier {
    img {
      height: 410px;
      width: 410px;
    }
  }
}
