.statsAndNumbers {
  display: flex;
  margin-bottom: 27px;
  .singleStat {
    flex: 1 1 auto;
    p {
      max-width: 185px;
      font-weight: normal;
      font-size: 14px;
      height: 32px;
      text-overflow: ellipsis;
      overflow: hidden;
      margin: 10px 0 0;
      line-height: 16px;
      color: #7987a1;
    }
    .name {
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;
      margin: 5px 0 0;
    }
    &_icon {
      height: 20px;
    }
    span.number {
      font-weight: bold;
      font-size: 20px;
      line-height: 23px;
    }
    .actual_stat {
      display: flex;
      gap: 5px;
    }
  }
}

.targets {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  .singleTarget {
    padding: 15px 33px;
    border-radius: 8px;
    background: #f4f5f8;
    flex: 1 1 auto;
    &_AdIcon {
      font-size: 30px;
    }

    .Ad-title {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      span {
        font-weight: bold;
        font-size: 16px;
        line-height: 19px;
      }
    }

    ul {
      list-style: none;
    }
    li {
      margin-left: 16px;
      padding-left: 15px;
      position: relative;
      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        height: 7px;
        width: 7px;
        border-radius: 50%;
        transform: translateY(-50%);
        border: 1px solid #7987a1;
      }
    }
    li:nth-child(1) {
      margin-left: 4px;
      &::before {
        border: none;
        background: #6c757e;
      }
    }
  }

  .details {
    display: flex;
    justify-content: space-between;
    column-gap: 2rem;
    &_way {
      flex: 1 1 30rem;
      width: 50%;
    }
    &_way_right {
      display: flex;
      flex-direction: column;
    }
    .second-span {
      font-weight: bold;
      font-size: 14px;
      line-height: 16px;
    }
    &_single {
      display: flex;
      justify-content: space-between;
      width: 100%;
      span {
        width: 50%;
        flex: 1 1 30rem;
        font-weight: normal;
        font-size: 16px;
        line-height: 19px;
      }
      span:nth-child(2) {
        width: 50%;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
      }
    }
  }
}
