.page-content {
  min-height: calc(100vh - 64px);
  flex: 1 1 auto;
  border: none;
  padding: 0px 34px 19px;
  position: relative;
  .showSideMenu {
    top: -1rem;
    .line {
      pointer-events: none;
    }
  }

  h1 {
    font-weight: 900;
    font-size: 21px;
    line-height: 25px;
  }
  .path {
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: $dividers;
    span {
      color: $headerColor;
    }
  }
  &__head {
    padding: 19px 7px;
  }

  &__single {
    max-width: 809px;
    margin: 1rem 0;

    // &:nth-child(2) {
    //   display: flex;
    // }
  }
  &__download {
    position: absolute;
    top: 34px;
    right: 43px;
    display: flex;
    gap: 0.5rem;
    img {
      width: 20px;
      height: 20px;
    }
    h4 {
      font-weight: 500;
      font-size: 14px;
      color: $danger;
    }
  }
  &__notice {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    // background: #000;|
    img {
      margin: 1rem;
      width: 20px;
      height: 20px;
    }
    span {
      color: $danger;
    }
  }
  &__buttons {
    display: flex;
    margin: 2rem 0;
    input {
      background: #f4f5f8;
      margin-left: auto;
    }
    button {
      color: white;
      background: #5600e3;
      margin: 0 0.5rem;
    }
    input,
    button {
      border: none;
      padding: 12px 23px;
      border-radius: 3px;
      pointer-events: none;
    }
  }
  .div {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    & > * {
      flex: 1 1 auto;
    }
  }
  label {
    font-weight: 500;
    font-size: 14px;
    margin-bottom: 10px;
  }
  textarea {
    font-weight: 500;
    font-size: 14px;
    max-width: 809px;
    width: 100%;
    // min-width: 20rem;
    padding: 12px 17px;
    height: 99px;
    border: 1px solid $dividers;
    box-sizing: border-box;
    border-radius: 3px;
  }
  select {
    padding-left: 14.5px;
    width: 455px;
    height: 40px;
    border: 1px solid #cdd4e0;
    border-radius: 3px;
  }
  .chineseText {
    text-align: right;
    color: $danger;
  }
}

.page-head {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .headbuttons {
    display: flex;
    align-items: center;
  }
  button {
    background: $BtnHover;
    height: 40px;
    padding-left: 13px;
    padding-right: 12px;
    border-radius: 3px;
    border: none;
  }
  .dbutton:nth-child(1) {
    button {
      border: 1px solid $dividers;
    }
  }
  .dbutton:nth-last-child(1) {
    button {
      color: white;
      background: $headerColor;
    }
  }
  .BackVector {
    background: none;
    font-weight: bold;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
  }
}
