.size-button {
  padding-left: 4px;
  padding-right: 4px;
  border: 1px solid $dividers;
  border-radius: 3px;
  display: flex;
  min-width: 37px;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  font-size: 12px;
  cursor: pointer;
  &:hover {
    background: lighten($headerColor, 15%);
    border: 1px solid lighten($headerColor, 15%);
    color: white;
  }
  &.active {
    background: $headerColor;
    border: 1px solid $headerColor;
    color: white;
  }
}
