.editProd {
  &_section {
    margin-bottom: 21px;
    &_head {
      display: flex;
      flex-wrap: wrap;
      gap: 5px;
      padding: 24px 20px 17px;
      border: 1px solid $dividers;
      background: $listHover;
      h2 {
        font-weight: bold;
        font-size: 16px;
        line-height: 19px;
      }
      span {
        padding: 2px 6px;
        font-weight: 500;
        font-size: 13px;
        height: 18px;
        background: $dividers;
        border-radius: 10px;
      }
    }
    &_body {
      padding: 18px 21px 24px;
      border: 1px solid $dividers;
      border-top: none;
      h3 {
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
      }
      p {
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        margin-bottom: 15px;
      }
      p,
      label.changeTitle {
        color: #7987a1;
      }
      label.changeTitle {
        font-weight: 500;
        font-size: 12px;
        line-height: 14px;
      }
      input.section1_input {
        width: 100%;
        background: #fff;
        padding: 13px 17px;
        height: 40px;
        border: 1px solid $dividers;
        border-radius: 3px;
        margin-top: 8px;
      }

      .subInputs {
        margin-top: 15px;
        display: flex;
        gap: 17px;
        &_single {
          flex: 1 1 auto;
        }
      }
      &.imageSection {
        max-height: 469px;
        overflow-y: scroll;
        width: 100%;
        display: grid;
        gap: 12px;
        grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
        // grid-template-rows: repeat(11, 130px);
        // & > * {
        //   width: 100%;
        //   //   height: 100%;
        // }
        img.poster {
          width: 100%;
          //   height: 100%;
        }
        .ImgChecker {
          position: relative;

          .eye {
            position: absolute;
            top: 10px;
            right: 8px;
            height: 10px;
            // display: none;
          }

          .overLay {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.6);
            display: none;
            z-index: 100;
          }
          &:hover {
            .overLay {
              display: block;
            }
          }
        }
        .checked {
          border: 3px solid $headerColor;
          position: relative;
          .checker {
            background: $headerColor;
            // display: none;
          }
          .ImgChecker__checkIcon {
            display: block;
          }
          .overLay {
            display: block;
            background: rgba(0, 0, 0, 0);
          }
        }

        .ImgChecker:nth-child(1) {
          grid-column: 1/3;
          grid-row: 1/3;
          //   grid-rows: 1/2;
          // gridcol
        }
      }
    }
    .AddMedia {
      border: 2px dashed $headerColor;
      display: flex;
      height: 130px;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      span {
        font-weight: 500;
        font-size: 13px;
        line-height: 15px;
        color: #97a3b9;
      }

      span:nth-child(1) {
        font-weight: bold;
        font-size: 14px;
        line-height: 16px;
        color: $headerColor;
      }
    }
    .preventionQue {
      margin-left: auto;
      display: flex;
      span {
        font-weight: 500;
        font-size: 13px;
        background: none;
        color: $headerColor;
      }
    }
  }
  .tableSection {
    padding: 0;
    overflow: auto;
    max-height: 353px;
    max-width: 1616px;
    width: calc(100vw - 322px + 14px);
    table {
      width: 100%;
      thead {
        width: 100%;
        border: none;
        color: #7987a1;
        tr {
          padding: 10px 14px;
        }
        // background: #000;
      }

      tr {
        // padding: 10px 0;
        border-bottom: 1px solid black;
      }
      input {
        // max-width: 100px;
        padding: 11px;
        height: 42px;
        border-radius: 3px;
        border: 1px solid $dividers;
      }

      input,
      td {
        font-weight: 500;
        font-size: 13px;
      }

      td {
        border-bottom: 1px solid $dividers;
        // border-left: none;
        // border-right: none;
        white-space: nowrap;
        padding: 10px 7px;
        // text-align: center;
        .switch {
          display: inline-flex;
        }
        .checker {
          position: static;
        }
        img {
          width: 40px;
          height: 40px;
        }
      }

      td.profit {
        color: $danger;
      }

      .currency {
        &_single {
          margin: 0 auto;
          display: flex;
          width: 95px;
          input {
            padding-left: 8px;
            width: 60px;
          }
          span {
            margin-left: -4px;
            background: #f4f5f8;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 42px;
            width: 36px;
            border: 1px solid $dividers;
            border-top-right-radius: 3px;
            border-bottom-right-radius: 3px;
          }
        }

        // input {
        //   padding: 0;
        // }
        // display: flex;
      }
    }
  }
  .DescSection {
    padding: 0;
  }
}

@media (max-width: 1000px) {
  .editProd {
    .tableSection {
      width: calc(100vw - 322px + 240px + 14px);
    }
  }
}
