.product-il {
  .input {
    border: 1px solid #cdd4e0;
    box-sizing: border-box;
    border-radius: 3px;
    width: 328px;
    height: 40px;
    position: relative;
    &_icon_search {
      position: absolute;
      left: 0.5rem;
      top: 50%;
      transform: translateY(-50%);
      color: $dividers;
    }
    // &_iconDiv {
    // }
    input {
      padding-left: 2rem;
      height: 100%;
      width: 100%;
      border: none;
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;
      &::placeholder {
        color: #596882;
      }
    }
  }
  &_search {
    display: flex;
    flex-wrap: wrap;
    button {
      margin: 0 0.5rem;
    }
  }
  button {
    padding: 10px 14px;
    background: #5600e3;
    border-radius: 3px;
    font-weight: 500;
    border: none;
    font-size: 14px;
    line-height: 16px;
    color: #ffffff;
  }
  .optionsFilter {
    margin: 11px 0;
    display: flex;
    height: 35px;

    .selector {
      margin-right: 4px;
      max-width: 181px;
      background: #f4f5f8;
      border-radius: 3px;
      display: flex;
      justify-content: center;
      align-items: center;
      &_iconDiv {
        background: #cdd4e0;
        border-radius: 3px 0px 0px 3px;
        width: 38px;
        min-width: 38px;
        height: 35px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex: 1 1 auto;
      }
      .unSelect {
        border: 1px solid $headerColor;
        background: transparent;
        .selector_icon {
          display: none;
        }
      }
      &_icon {
        color: white;
        width: 8px;
      }
      &_iconBox {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 14px;
        height: 14px;
        background: $headerColor;
        border-radius: 3px;
      }
      span {
        flex: 1 1 auto;
        padding: 0 12px;
        white-space: nowrap;
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
      }
    }

    button {
      margin: 0 4px;
    }
  }
  &__single {
    padding: 13px;
    min-height: 114px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    border: 1px solid $dividers;
    border-top: none;

    &:nth-child(1) {
      border-top: 1px solid $dividers;
    }

    .buttons,
    .cost {
      display: flex;
      font-size: 14px !important;
    }

    .buttons {
      opacity: 0;
      pointer-events: none;

      button {
        cursor: pointer;
      }

      button.simple {
        margin: 0 4px;
      }
      button.redBtn {
        background: #f56c6c;
      }
    }
    .cost {
      font-weight: 500;
      color: $dividers;
      &-actual {
        color: black;
        font-weight: 700;
      }
    }
    &:hover {
      background: $listHover;
      .buttons {
        opacity: 1;
        pointer-events: auto;
      }
      .title {
        color: $headerColor !important;
      }
    }
  }
  .text {
    flex: 1 1 10rem;
    .supplierAndStatus {
      margin-top: 2rem;
      img {
        height: 17px;
      }
    }
    .supply {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      max-width: 23rem;
      width: auto;
    }
    .supplier {
      // margin-right: 3rem;
      display: inline;
      color: #97a3b9;
      img {
        position: relative;
        top: 0.2rem;
      }
    }
    .supplier_actual {
      color: black;
      font-weight: 500;
      font-size: 13px;
      line-height: 15px;
    }
    .title {
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;
      color: #23282f;
    }
    .catagory {
      font-weight: 500;
      font-size: 13px;
      line-height: 15px;
      color: #23282f;
    }
    .brandType {
      font-weight: 500;
      font-size: 13px;
      line-height: 30px;
      color: $danger;
    }
  }

  .checkAndImage {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    input,
    img {
      margin-right: 17px;
    }

    input {
      position: relative;
      top: 2.5rem;
    }
  }

  .status {
    margin-top: 14px;

    &_single {
      display: flex;
      align-items: center;
      font-weight: 500;
      font-size: 13px;
      line-height: 15px;
      .circlePointer {
        height: 8px;
        border-radius: 50%;
        width: 8px;
        margin-right: 5px;
      }
    }
  }

  .costAndButton {
    padding: 4px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
  }
  .action {
    button {
      background: transparent;
      color: #23282f;
      border: 1px solid #23282f;
      white-space: nowrap;
    }
  }
}
