.contact-info {
  h2 {
    font-weight: bold;
    font-size: 14px;
    margin-bottom: 1rem;
  }
  .emails {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    gap: 23px;
    .singleInput {
      width: 50%;
      flex: 1 1 auto;
    }
  }
  form {
    max-width: 683px;
  }
  input {
    margin: 0.5rem 0;
    border-radius: 3px;
    border: 1px solid $dividers;
    height: 36px;
    width: 100%;
    padding-left: 10px;
  }
  .singleInput {
    margin: 0.7rem 0;
  }
  span {
    font-weight: normal;
    font-size: 12px;
    color: #7987a1;
  }
  .localeInfo {
    width: 100%;
    display: flex;
    gap: 25px;
    .singleInput {
      width: 33.33%;
      flex: 1 1 auto;
    }
  }
}
