.switch {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  position: relative;
  height: 18px;
  width: 43px;
  //   transform: scale(5);
  display: flex;
  align-items: center;
  width: auto;
  padding: 2px;
  border-radius: 18px;
  background: $headerColor;
  transition: 0.3s ease;

  .switch_Circle {
    transition: 0.3s ease;
    position: relative;
    width: 14px;
    height: 100%;
    margin-left: 3px;
    z-index: 50;
    border-radius: 50%;
    top: 0;
    background: white;
  }
  .onSwitch {
    position: relative;
    left: 1px;
    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
    color: #ffffff;
    transform: scale(0.79);
  }

  .dontShow {
    opacity: 0;
  }

  &.initial {
    // background: $dividers;
    background: darken($dividers, 20);
    b.switch_Circle {
      transform: translateX(-130%);
    }
  }
}
