.account-page {
  .page-content__head {
    padding-left: 0;
  }
  .main-page_head_accDet {
    margin-bottom: 20px;
    h1 {
      font-weight: bold;
      font-size: 16px;
      white-space: normal;
    }
  }
  .pageDesc {
    font-weight: bold;
    font-size: 14px;
    margin-bottom: 17px;
  }
  &_info {
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    width: 100%;
    & + button {
      background: $headerColor;
      border: none;
      color: white;
      padding: 12px 11px;
      font-weight: 500;
      font-size: 14px;
      margin: 15px 0;
    }
  }
  &_singleInfo {
    .title {
      width: 99px + 22px;
      color: #7987a1;
    }
    .pass,
    .details {
      width: calc(100% - (121px));
      font-weight: 500;
    }
    .pass {
      button {
        border: none;
        background: none;
        color: $headerColor;
      }
    }
  }
  hr {
    border: none;
    border-top: 1px solid $dividers;
    margin: 29px 0 22px;
  }
  input:focus {
    background: #eee1ff;
    border: 2px solid $headerColor;
  }
}

