.main-page {
  padding: 27px 29px;
  &_head {
    display: flex;
    flex-wrap: wrap;
    &_accDet {
      .logo {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 45px;
        width: 45px;
        font-weight: bold;
        font-size: 25px;
        color: white;
        border-radius: 50%;
        background: #ff7300;
      }
      .logo.notLogedIn {
        background: #7987a1;
      }
      display: flex;
      gap: 10px;
      button {
        margin-left: 10px;
        border: none;
        background: none;
        color: $headerColor;
        font-weight: 500;
        font-size: 13px;
      }

      h1 {
        white-space: nowrap;
        font-weight: bold;
        font-size: 16px;
        span {
          color: $danger;
        }
      }
      p {
        font-weight: normal;
        font-size: 13px;
        color: #7987a1;

        span {
          color: #ff7300;
        }
      }
      .text {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
      }
      .createAnAccount {
        height: 40px;
        min-width: 145px;
        border-radius: 3px;
        background: $headerColor;
        color: white;
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
      }
    }
    &_custRatings {
      margin-left: 0;
    }
    .ratings {
      display: flex;
      gap: 4.5px;
      .stars_icon {
        width: 17px;
        height: 17px;
        color: #ff7300;
      }
      .stars {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 4.5px;
      }
      .unfilled {
        color: #97a3b9;
      }
      p.reviews {
        font-weight: normal;
        font-size: 13px;
      }
    }
    h3 {
      white-space: nowrap;
      font-weight: 500;
      font-size: 13px;
      line-height: 15px;
    }
    &_right {
      display: flex;
      flex-wrap: wrap;
      gap: 48px;
      margin-left: auto;
    }
    &_custRatings,
    &_priceReps {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      justify-content: space-between;
    }
    &_priceReps {
      span {
        font-family: "Archivo Black", sans-serif;
        font-weight: normal;
        font-size: 20px;
        line-height: 22px;
      }
    }
    .angle_icon {
      margin-left: 7px;
      color: #cdd4e0;
    }
  }

  &_body {
    padding: 29px 0;
    border-radius: 3px;
    &_head {
      padding: 17px 30px;
      border: 1px solid $dividers;
      background: #f4f5f8;
    }
    &_body {
      display: flex;
      flex-wrap: wrap;
      gap: 2rem;
      justify-content: space-between;
      align-items: center;
      padding: 30px;
      border-radius: 3px;
      border: 1px solid $dividers;
    }
    .todayIncomeComponent {
      display: flex;

      gap: 11px;
      flex-direction: column;
      span {
        font-weight: 500;
        font-size: 14px;
      }
      span.number {
        font-family: Archivo Black;

        font-size: 32px;
        line-height: 35px;
      }
      img.greenGraph {
        width: 12px;
        height: 13.6px;
        margin-right: 6px;
      }
      img.grayGraph {
        max-width: 100%;
      }
      span.number_2 {
        font-weight: 500;
        font-size: 14px;
      }
    }
  }
}

.main_status {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  // display: none;
  span {
    color: $danger;
    font-weight: 700;
  }
  p {
    margin-top: 6px;
    font-weight: normal;
    font-size: 14px;
  }
  button {
    border: none;
    background: none;
    margin-left: 6px;
    color: $headerColor;
    font-weight: 700;
  }

  .iconDiv {
    height: 20px;
    width: 20px;
    margin-right: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.3rem;
    border-radius: 50%;
  }
  .status-icon {
    height: 12px;
    color: white;
  }
  &.done {
    .iconDiv {
      background: #3bb001;
    }
  }
  &.warn {
    .iconDiv {
      background: $headerColor;
    }
  }
}

@media (max-width: 1040px) {
  .main-page {
    &_head {
      display: flex;

      flex-wrap: wrap;
      &_accDet {
        margin: 0 auto;
      }
      &_right {
        margin-top: 10px;
        width: 100%;
        justify-content: space-between;
        margin-left: 0;
      }
    }
  }
}
