.team-page {
  table {
    width: 100%;
  }
  .company {
    margin-bottom: 21px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    .logo {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 45px;
      height: 45px;
      border-radius: 50%;
      background: #ff7300;
      color: white;
      font-weight: bold;
      font-size: 25px;
    }
    .text {
      padding: 0 10px;
      h3 {
        font-weight: bold;
        font-size: 16px;
        line-height: 19px;
      }
      span {
        font-weight: normal;
        font-size: 13px;
        line-height: 15px;

        color: #7987a1;
      }
    }
  }
  .teamHead {
    margin-bottom: 19px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    button {
      margin-top: 5px;
      padding: 12px 17px;
      border: none;
      border-radius: 3px;
      color: white;
      background: $headerColor;
      white-space: nowrap;
    }
  }
  //   .teamTable {
  // }
  .tableHead {
    background: $listHover;
    border: 1px solid $dividers;
    button {
      background: transparent;
      width: 128px;
      height: 48px;
      border: none;
      border-right: 1px solid $dividers;
      font-weight: 500;
      font-size: 16px;

      &.active {
        background: white;
        border-bottom: 5px solid $headerColor;
      }
    }
  }
  .tableBody {
    overflow: auto;
    width: calc(100vw - 308px);
    max-width: 1612px;
    max-height: 17rem;
    border-right: 1px solid $dividers;
    border-left: 1px solid $dividers;

    &_single {
      // min-width: 1030px;
      border-bottom: 1px solid $dividers;
      padding: 10px 24px;
      display: flex;
      flex-wrap: nowrap;
      justify-content: center;
      align-items: center;
      gap: 9px;
      img {
        border-radius: 50%;
        height: 40px;
        width: 40px;
      }
    }
    & * {
      white-space: nowrap;
    }
    .details {
      display: flex;
      flex-wrap: nowrap;
      flex: 1 1 auto;
      justify-content: space-around;
      align-items: center;
      gap: 2rem;
      span {
        white-space: nowrap;
        // flex: 1 1 10rem;
        font-size: 13px;
        &.btn {
          display: flex;
          justify-content: center;
          align-items: center;
          background: $headerColor;
          color: white;
          width: 54px;
          height: 23px;
          border-radius: 20px;
        }
      }
      .hide {
        opacity: 0;
      }
    }

    .actions {
      flex: 1 1 auto;
      display: flex;
      justify-content: center;
      align-items: center;

      button {
        border: none;
        background: none;
        margin: 0 17px;
        font-weight: 500;
        font-size: 13px;
        color: $headerColor;
      }

      button.remove {
        color: $danger;
      }

      button:nth-child(1) {
        margin-left: auto;
      }
    }
  }
}

@media (max-width: 1000px) {
  .team-page {
    .tableBody {
      width: calc(100vw - 308px + 240px);
    }
  }
}
