.currency-page {
  .page-head {
    h1 {
      text-transform: capitalize !important;
    }
  }
  .page-content__body {
    padding: 0 7px;
  }
  .currPara {
    margin-bottom: 15px;
  }
  &__single {
    display: flex;
    align-items: center;
    .input {
      display: flex;
      align-items: center;
    }
    p {
      font-weight: 500;
      font-size: 13px;
    }
    span {
      color: $danger;
    }
  }
  select {
  }
  select,
  input {
    width: 100%;
    max-width: 262px;
    margin: 10px;
    height: 36px;
    border: 1px solid $dividers;
    border-radius: 3px;
    padding: 0 10px;
  }
}
